
    import { defineComponent } from 'vue';
    import { clearImageSrc, getImageSrc } from '@/store/imageStore';

    export default defineComponent({
        name: 'ImagePreview',
        setup() {
            const close = () => {
                clearImageSrc();
            };

            return {
                close,
                src: getImageSrc(),
            };
        },
    });
