
    import { Contact } from '@/types';
    import { defineComponent, ref, computed } from 'vue';

    export default defineComponent({
        props: {
            modelValue: {
                type: String,
                required: false,
            },
            placeholder: {
                type: String,
                required: false,
                default: 'Enter text here.',
            },
            data: {
                type: Array,
                required: true,
            },
            dropdownClass: {
                type: String,
                required: false,
                default:
                    'absolute w-full z-50 bg-white border border-gray-300 mt-1 mh-48 overflow-hidden overflow-y-scroll rounded-md shadow-md',
            },
            error: {
                type: String,
                default: '',
            },
        },
        emits: ['update:modelValue', 'clicked'],
        setup(props, { emit }) {
            const showOptions = ref(false);
            const chosenOption = ref('');
            const searchTerm = ref('');

            const reset = () => {
                emit('update:modelValue', '');
                chosenOption.value = '';
                searchTerm.value = '';
            };

            const handleInput = evt => {
                emit('update:modelValue', evt.target.value);
                showOptions.value = true;
            };

            const handleClick = item => {
                console.log(chosenOption.value);
                chosenOption.value = item.id;
                searchTerm.value = item.id;
                emit('update:modelValue', item.id);
                showOptions.value = false;
                emit('clicked');
            };

            const clickedOutside = () => {
                showOptions.value = false;

                if (!chosenOption.value) {
                    emit('update:modelValue', '');
                }
            };
            const searchResults = () => {
                return props.data.filter((item: Contact) => {
                    return item.id
                        .toLowerCase()
                        .includes(searchTerm.value.toLowerCase());
                });
            };

            return {
                reset,
                handleInput,
                handleClick,
                clickedOutside,
                showOptions,
                chosenOption,
                searchTerm,
                searchResults,
            };
        },
    });
