
    import { usechatsActions } from '@/store/chatStore';
    import Button from "@/components/Button.vue"

    export default {
        name: 'ChatRequestList',
        components: {
           Button
        },
        props: {
            chatRequests: {},
        },
        setup() {
            const acceptChatRequest = id => {
                const { acceptChat } = usechatsActions();
                acceptChat(id);
            };
            return {
                acceptChatRequest,
            };
        },
    };
