
    import { fetchGif } from '@/services/gifService';
    import { ref, watch } from 'vue';

    export default {
        name: 'GifSelector',
        emits: ['close'],
        setup(props) {
            const gifs = ref([]);
            const searchTerm = ref('');

            fetchGif().then(gs => {
                gifs.value = gs.data.map(g => g.images.original.url);
            });
            watch(searchTerm, st => {
                fetchGif(st).then(gs => {
                    gifs.value = gs.data.map(g => g.images.original.url);
                });
            });

            return { gifs, searchTerm };
        },
    };
