
    import { defineComponent } from 'vue';
    import { login } from '@/services/authService';

    const loginAndPush = async () => {
        console.log('Attempting to login ...');
        await login();
    };

    export default defineComponent({
        name: 'Home',
        mounted() {
            setTimeout(() => {
                loginAndPush();
            }, 3200);
        },
        setup() {
            return {
                name: window.location.host.split('.')[0],
                loginAndPush,
            };
        },
    });
